@import url(//db.onlinewebfonts.com/c/557661ea24d10fa4d2d85f60f04411d8?family=Shentox);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1493ff;
  background-image: radial-gradient(#0facfd 16%, #1493ff 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


.home__page--wrapper{display:flex;width:100%;height:100vh;justify-content:center;align-items:center;flex-direction:column}.home__page--wrapper .home__page--banner{display:flex;align-self:center}.home__page--wrapper .home__page--banner .home__page--title{max-width:3.15em;margin:0;font-size:10vh;line-height:.85;text-align:right;position:relative;font-family:"Shentox";color:#fff}.home__page--wrapper .home__page--banner .home__page--title .home__page--colored{margin-top:-0.025em;margin-bottom:-0.25em;display:block;color:#3de585;font-size:1em;background:-webkit-linear-gradient(270deg, #81edaf 13%, #33e37e 44%, #1e9448 90%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;position:relative;text-shadow:none}.home__page--wrapper .home__page--banner .home__page--title .home__page--colored:before{content:"win";position:absolute;z-index:-1;text-shadow:-0.025em -0.025em 4px rgba(0,0,0,0.25)}.home__page--wrapper .home__page--banner .home__page--player{height:225px}.home__page--wrapper .home__page--button{margin:0;padding:1.5em 0;width:45%;margin-top:20px;max-width:300px;border-radius:6px;border:2px solid #1493ff;background:#1f375b;font-family:"Shentox";font-size:.75rem;line-height:1;text-align:center;text-transform:uppercase;box-shadow:0 4px 6px rgba(0,0,0,0.4);-webkit-transition:all .2s ease;text-decoration:none;color:#fff;transition:all .2s ease;position:relative;z-index:1}.home__page--wrapper .home__page--button:after{content:"";position:absolute;z-index:-1;top:-2px;right:-2px;bottom:-2px;left:-2px;opacity:0;border-radius:6px;box-shadow:0 12px 16px 4px rgba(0,0,0,0.2);transition:all .2s ease}.home__page--wrapper .home__page--button:hover{transform:scale(1.01);color:#30e37b}

.player__page--wrapper{display:flex;flex-direction:column;cursor:pointer;transition:transform 0.4s}.player__page--wrapper .player__card{width:300px;height:300px;border:1px solid #1493ff;overflow:hidden;background-image:url(/duelingfans/static/media/bg.0a12e54a.png);background-size:cover;background-repeat:no-repeat;box-shadow:0 0 6px 0px #1f375b;margin:0px 10px}.player__page--wrapper .player__card .player__card--image{width:100%}.player__page--wrapper .player__name{font-family:'Shentox';font-size:1em;color:#fff;margin:0px 10px;background-color:#1f375b;padding:10px}.player__page--wrapper:hover{transform:scale(1.02)}

.duel__page--wrapper{display:flex;width:100%;height:100vh;flex-wrap:wrap;justify-content:space-evenly;align-items:center;flex-direction:column}.duel__page--wrapper .duel__basketball--wrapper{width:500px}.duel__page--wrapper .duel__basketball--wrapper .duel__basketball{width:100%;height:70px;background-image:url(data:image/png;base64,);background-repeat:no-repeat;background-position:center;background-position-x:0;background-size:200%}.duel__page--wrapper .duel__player--wrapper{flex-direction:row;display:flex}

.navbar{width:100%;position:fixed;top:0;left:0}.navbar .navbar__list{display:flex;width:100%;padding:0;margin:0;background:#1f375b;justify-content:center}.navbar .navbar__list--items{display:inline-flex;height:50px;background:#1f375b;width:50%}.navbar .navbar__list--items:hover{background:rgba(31,55,91,0.87)}.navbar .navbar__list--items a{display:flex;text-decoration:none;color:#fff;width:100%;height:100%;justify-content:center;align-items:center;font-family:"Shentox";letter-spacing:1.2px}.navbar .navbar--logo{padding:10px;width:50px}

